import React from "react";
import "../App.css";
import Plx from "react-plx";
import Typing from "react-typing-animation";

class AboutMe extends React.Component {



  render() {
    return (
      <div style={{ height: "70vh", verticalAlign: "middle", paddingLeft: "10em", paddingRight: "10em"}}>
        <div className="center-vert">
          <Typing startDelay={1500} speed={20}>
            <h1
              style={{
                fontSize: "9vh",
                fontWeight: "lighter",
                color: " #22a39f"
              }}
            >
              Hello.
              <Typing.Delay ms={500} />
              &nbsp;My name is Joseph Antony.
              <Typing.Delay ms={1000} />
              <Typing.Backspace count={35} speed={10} />
              I am a Software Engineer.
              <Typing.Delay ms={1000} />
              &nbsp;Apparently.
              <Typing.Delay ms={1000} />
              <Typing.Backspace count={50} speed={10} />
              I've built this site, mainly to show off my stuff.
              <Typing.Delay ms={1000} />
              <Typing.Backspace count={63} speed={5} />
              To be honest though, this is my first big website.
              <Typing.Delay ms={1500} />
              <Typing.Backspace count={50} speed={5} />
              You can kinda tell with the lack of optimization.
              <Typing.Delay ms={1500} />
              <Typing.Backspace count={50} speed={5} />
              I was going to end it on a more positive note, but we can figure out the script later.
            </h1>
          </Typing>
        </div>
      </div>
    );
  }
}

export default AboutMe;
